.slide-i-l-1 {
    animation: slide-i-l 0.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
    animation-delay: 0.2s;
}

.slide-i-l-2 {
    animation: slide-i-l-2 0.75s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
    animation-delay: 0.5s;
}

.slide-i-l {
    animation: slide-i-l 0.75s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
    animation-delay: 1.5s;
}

.menu__section {
    animation: slide-i-b 0.75s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;

    // sticky nav
    // &:nth-child(1) {
    //     animation-delay: 4s;
    // }
    &:nth-child(2) {
        animation-delay: 1s;
    }

    &:nth-child(3) {
        animation-delay: 1.25s;
    }
}

.main-bg__content {

    img,
    h1,
    h2,
    ul,
    a {
        animation: slide-in-bottom 0.75s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
    }

    img,
    h1,
    h2 {
        animation-delay: .5s;
    }

    a {
        animation-delay: .85s;
    }

    ul {
        animation-delay: 1.5s;
    }


    @keyframes slide-in-bottom {
        0% {
            transform: translateY(50px);
            opacity: 0;
        }

        100% {
            transform: translateY(0);
            opacity: 1;
        }
    }
}

@keyframes slide-i-l {
    0% {
        transform: translateX(-100px);
        opacity: 0;
    }

    100% {
        transform: translateX(0);
        opacity: 1;
    }
}

@keyframes slide-i-l-2 {
    0% {
        transform: translateX(-30px);
        opacity: 0;
    }

    100% {
        transform: translateX(0);
        opacity: 1;
    }
}

@keyframes slide-i-b {
    0% {
        transform: translateY(50px);
        opacity: 0;
    }

    100% {
        transform: translateY(0);
        opacity: 1;
    }
}
