.menu {
    &__section {
        // margin-top: 75px;
        padding-top: 75px;
    }

    &__section h2,
    &__sub-header {
        font-family: $font-scnd;
        font-weight: 300;
        text-transform: uppercase;
    }

    &__section h2 {
        font-size: 1.25em;
        color: var(--main-yello);
        letter-spacing: 5px;
    }

    &__section h2,
    &__sub-header {
        text-align: center;
    }

    &__section h2 {
        margin-right: 2.5em;
    }

    &__sub-header {
        margin-right: 5em;
        // width: 82%;
    }

    &__header {}

    &__sub-header {
        margin-bottom: 5px;
        font-size: .75em;
        color: var(--gray);
        letter-spacing: 3px;
    }

    &__list {
        margin-top: 30px;

        &>* {
            &:not(:first-child) {
                margin-top: 35px;
            }
        }
    }

    &__item {
        text-transform: capitalize;
        color: var(--main-yello);
    }

    &__item-wrap {
        display: flex;
        justify-content: flex-end;
    }

    &__item-title-wrap {
        display: flex;
        align-items: center;
        justify-content: center;
        flex: 1;
        flex-direction: column;

        &>* {
            &:not(:first-child) {
                margin-top: 10px;
            }
        }
    }

    &__item-title {
        position: relative;
        width: 100%;
        text-align: center;
        font-size: 1.1em;
        padding: 0 1.5em;

        span {
            position: absolute;
            top: 50%;
            margin-left: 5px;

            font-family: $font-scnd;
            color: var(--gray);
            font-size: .60em;

            transform: translateY(-50%);
        }
    }

    &__item-description {
        padding: 0 2em;
        color: var(--gray);
        font-size: .85em;
        // letter-spacing: .5px;

        text-transform: lowercase;
        font-style: italic;
        text-align: center;
    }

    &__item-price {
        display: flex;
        flex-direction: column;
        justify-content: center;

        padding-left: 5px;
        width: 4em;

        font-size: .95em;
        font-family: $font-scnd;
        color: var(--white);
        border-left: 2px solid var(--main-yello);

        span {
            // display: block;
            text-align: center;
        }

        &--ob {
            word-break: break-all;
            font-family: $font-scnd;
            color: var(--gray);
            font-size: .75em;
            text-transform: uppercase;
        }
    }

    &__rub {
        display: flex;
        justify-content: center;

        span {
            position: relative;
            margin-left: 3px;

            &::after {
                content: 'Р';
            }

            &::before {
                content: '';
                position: absolute;
                top: 60%;
                left: 0;
                width: 6px;
                height: 1.5px;
                background-color: currentColor;
                transform: translateY(-50%);
            }
        }
    }

    &__sub-list {}

    &__sub-item {
        margin-top: 10px;

        &--25 {
            margin-top: 25px;
        }
    }

    &__special-text {
        margin-top: 35px;

        font-size: 1em;
        text-align: center;
        color: var(--gray);
    }

    &__special-link {
        display: flex;
        align-items: center;
        justify-content: center;

        margin-top: 10px;
        color: var(--main-yello);
        text-decoration: none;

        span {
            font-size: 1.2em;
            margin-left: 5px;
        }
    }

    &__call-btn {
        z-index: 1;
        position: fixed;
        right: 15px;
        bottom: 25px;

        display: flex;
        align-items: center;
        justify-content: center;

        width: 50px;
        height: 50px;

        background-color: var(--main-yello);
        border-radius: 50%;

        animation: heartbeat 1.5s ease-in-out infinite both;
    }

    @keyframes heartbeat {
        from {
            transform: scale(1);
            transform-origin: center center;
            animation-timing-function: ease-out;
        }

        10% {
            transform: scale(0.91);
            animation-timing-function: ease-in;
        }

        17% {
            transform: scale(0.98);
            animation-timing-function: ease-out;
        }

        33% {
            transform: scale(0.87);
            animation-timing-function: ease-in;
        }

        45% {
            transform: scale(1);
            animation-timing-function: ease-out;
        }
    }
}
