@charset "UTF-8";
:root { --main-yello: #FCBD63; --black: #020202; --gray: #757575; --white: #ffffff; }

.icon-facebook { width: 0.53em; height: 1em; fill: initial; }

.icon-facebook { width: 0.53em; height: 1em; fill: initial; }

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video { margin: 0; padding: 0; border: 0; vertical-align: baseline; }

html { -ms-text-size-adjust: 100%; -webkit-text-size-adjust: 100%; }

img, fieldset, a img { border: none; }

input[type="text"], input[type="email"], input[type="tel"], textarea { -webkit-appearance: none; }

input[type="submit"], button { cursor: pointer; }

input[type="submit"]::-moz-focus-inner, button::-moz-focus-inner { padding: 0; border: 0; }

textarea { overflow: auto; }

input, button { margin: 0; padding: 0; border: 0; }

div, input, textarea, select, button, h1, h2, h3, h4, h5, h6, a, span, a:focus { outline: none; }

ul, ol { list-style-type: none; }

table { border-spacing: 0; border-collapse: collapse; width: 100%; }

html { box-sizing: border-box; }

*, *:before, *:after { box-sizing: inherit; }

@media (prefers-reduced-motion: reduce) { * { transition: none !important; } }

@font-face { font-family: product; src: url("../fonts/product-sans.woff2"); font-weight: 400; font-style: normal; font-display: swap; }

@font-face { font-family: valencia; src: url("../fonts/valencia.woff2"); font-weight: 400; font-style: normal; font-display: swap; }

body { padding: 30px 10px; padding-bottom: 100px; font-size: 14px; line-height: 1.4; font-family: "product", sans-serif; background: #020202 url(./../img/bg.png) repeat-y; background-size: contain !important; }

@media only screen and (min-width: 375px) { body { font-size: 17px; } }

@media only screen and (min-width: 460px) { body { font-size: 22px; } }

.bg-p { position: relative; }

.bg-p--1 { padding-bottom: 250px; }

.bg-p--2 { padding-bottom: 300px; }

.bg-i { z-index: -1; position: absolute; left: -10px; right: -10px; width: calc(100% + 20px); height: 100%; background-size: contain; background-repeat: no-repeat; background-position: bottom; }

.bg-i--1 { bottom: -20px; background-image: url(./../img/bg-1.png); }

.bg-i--2 { bottom: -125px; background-image: url(./../img/bg-2.png); }

.bg-i--3 { bottom: -125px; background-image: url(./../img/bg-3.png); }

.bg-i--4 { bottom: -125px; background-image: url(./../img/bg-4.png); }

.sticky-nav { z-index: 5; position: sticky; top: 65px; left: 0; }

.sticky-nav__list { position: absolute; left: -5px; top: -5px; transform-origin: left top; transform: rotate(-90deg) translateX(-100%); display: flex; }

.sticky-nav__list a { display: block; padding: 2px 5px 20px 5px; text-decoration: none; color: var(--gray); font-family: "valencia", sans-serif; font-size: .55em; letter-spacing: 2px; text-transform: uppercase; }

.sticky-nav__list a:active, .sticky-nav__list a:hover { color: var(--main-yello); }

.main-bg-body { padding: unset; }

.main-bg-body main { z-index: -1; position: fixed; top: 0; bottom: 0; left: 0; right: 0; width: 100vw; background: #020202 url(./../img/bg.png) repeat-y; background-size: contain !important; }

.main-bg { z-index: -1; position: fixed; top: 0; bottom: 0; left: 0; right: 0; opacity: .25; padding: unset; margin: unset; background-color: #e74c3c; animation: bg-color 10s infinite; -webkit-animation: bg-color 10s infinite; background-image: unset; }

.main-bg-begin__wrap { display: flex; align-items: center; width: 100%; height: 100%; }

.main-bg-begin { padding: 7px 45px; font-size: 1.25em; color: var(--gray); background-color: var(--white); border-radius: 50px; text-decoration: none; }

.main-bg__content { display: flex; align-items: center; flex-direction: column; justify-content: center; padding: 10px; padding-top: 25px; }

.main-bg__content img { width: 125px; }

.main-bg__content h1 { margin-top: auto; }

.main-bg__content h1, .main-bg__content h2, .main-bg__content h3, .main-bg__content span { font-weight: 300; text-transform: uppercase; text-align: center; }

.main-bg__content h1 { margin-top: 35px; color: var(--main-yello); font-size: 1.25em; }

.main-bg__content h3 { color: var(--main-yello); font-size: 1em; }

.main-bg__content h2, .main-bg__content span { margin-top: 25px; margin-bottom: 25px; font-family: "valencia", sans-serif; color: var(--gray); font-size: .65em; letter-spacing: 3px; }

.main-bg__content span { display: block; margin-top: unset; margin-bottom: unset; margin-left: 5px; }

.main-bg__content ul { margin-top: 25px; }

.main-bg__content ul li { margin-top: 15px; }

.main-bg__content ul li div { display: flex; align-items: center; justify-content: center; }

.main-bg__content ul li div img { width: 1em; }

.main-bg__content li a { text-decoration: none; color: currentColor; }

.main-bg__content h3 { margin: unset; }

.main-bg .bounce-in-top { animation: bounce-in-top 1.1s both; animation-delay: 1s; }

@keyframes bounce-in-top { 0% { transform: translateY(-500px);
    animation-timing-function: ease-in;
    opacity: 0; }
  38% { transform: translateY(0);
    animation-timing-function: ease-out;
    opacity: 1; }
  55% { transform: translateY(-65px);
    animation-timing-function: ease-in; }
  72% { transform: translateY(0);
    animation-timing-function: ease-out; }
  81% { transform: translateY(-28px);
    animation-timing-function: ease-in; }
  90% { transform: translateY(0);
    animation-timing-function: ease-out; }
  95% { transform: translateY(-8px);
    animation-timing-function: ease-in; }
  100% { transform: translateY(0);
    animation-timing-function: ease-out; } }

.main-bg #bg-wrap { width: 100vw; height: 100vh; }

.main-bg #bg-wrap svg { width: 100%; height: 100%; }

