.header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex: 1;

    padding-left: .5em;
    padding-right: .5em;

    &__container-wrap {
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        z-index: 2;

        transition: transform .5s;
        background-color: rgba(0, 0, 0, 0.75);
        backdrop-filter: blur(3px);
        // &--scroll-up {
        //     transform: translateY(-89px) !important;
        // }
    }

    &__container {
        display: flex;
        align-items: center;
        justify-content: space-between;

        min-height: 60px;
    }

    &__logo {
        // width: 85px;
        // height: 39px;
        width: 5.5em;
        height: 2.5em;
    }

    &__logo-wrap {
        a {
            display: block;
        }
    }

    &__nav {}

    &__nav-list {
        display: flex;
        font-size: 1em;
    }

    &__nav-item {
        a {
            display: block;
            padding: 3px 15px;
            border-radius: 50px;
            text-decoration: none;
            color: var(--gray);
        }

        &--active a {
            background-color: var(--main-yello);
            color: var(--black);
        }
    }
}
