.main-bg-body {
    // height: 100vh;
    padding: unset;

    main {
        z-index: -1;
        position: fixed;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        width: 100vw;
        // height: 100vh;
        background: #020202 url(./../img/bg.png) repeat-y;
        background-size: contain !important;
    }
}

.main-bg {
    z-index: -1;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;

    opacity: .25;

    padding: unset;
    margin: unset;
    background-color: #e74c3c;
    animation: bg-color 10s infinite;
    -webkit-animation: bg-color 10s infinite;
    background-image: unset;

    &-begin__wrap {
        display: flex;
        align-items: center;
        width: 100%;
        height: 100%;
    }

    &-begin {
        // z-index: 5;
        // position: absolute;
        padding: 7px 45px;

        font-size: 1.25em;
        color: var(--gray);
        background-color: var(--white);

        border-radius: 50px;
        text-decoration: none;
    }

    &__content {
        display: flex;
        align-items: center;
        flex-direction: column;
        justify-content: center;

        padding: 10px;
        padding-top: 25px;

        img {
            width: 125px;
        }

        h1 {
            margin-top: auto;
        }

        h1,
        h2,
        h3,
        span {
            font-weight: 300;
            text-transform: uppercase;
            text-align: center;
        }

        h1 {
            margin-top: 35px;
            color: var(--main-yello);
            font-size: 1.25em;
        }

        h3 {
            color: var(--main-yello);
            font-size: 1em;
        }

        h2,
        span {
            margin-top: 25px;
            margin-bottom: 25px;
            font-family: $font-scnd;
            color: var(--gray);

            font-size: .65em;
            letter-spacing: 3px;
        }

        span {
            display: block;
            margin-top: unset;
            margin-bottom: unset;
            margin-left: 5px;
        }

        ul {
            margin-top: 25px;
        }

        ul li {
            margin-top: 15px;
        }

        ul li div {
            display: flex;
            align-items: center;
            justify-content: center;

            img {
                width: 1em;
            }
        }

        li a {
            text-decoration: none;
            color: currentColor;
        }

        h3 {
            margin: unset;
        }
    }

    .bounce-in-top {
        animation: bounce-in-top 1.1s both;
        animation-delay: 1s;
    }

    @keyframes bounce-in-top {
        0% {
            transform: translateY(-500px);
            animation-timing-function: ease-in;
            opacity: 0;
        }

        38% {
            transform: translateY(0);
            animation-timing-function: ease-out;
            opacity: 1;
        }

        55% {
            transform: translateY(-65px);
            animation-timing-function: ease-in;
        }

        72% {
            transform: translateY(0);
            animation-timing-function: ease-out;
        }

        81% {
            transform: translateY(-28px);
            animation-timing-function: ease-in;
        }

        90% {
            transform: translateY(0);
            animation-timing-function: ease-out;
        }

        95% {
            transform: translateY(-8px);
            animation-timing-function: ease-in;
        }

        100% {
            transform: translateY(0);
            animation-timing-function: ease-out;
        }
    }


